.ModalImg{
  max-width: 80%;
  box-shadow: 0.33333rem 0 1.9rem rgba(87, 87, 87, 0.8);
  border-radius: 5%;
}
.modalStyle{
  border-radius: 3% !important;
}
.imgCard{
  max-width: 100%;
  height: auto;
}
.table{
  table-layout: fixed;
  word-wrap: break-word;
}
.flexstyle{
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.minWidth{
  min-width: 100px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


